//////// FORM VALIDATION BASE FUNCTION //////////////////////////////
export const validateForm = formSelector => {
  const formElement = document.querySelector(formSelector)
  let success = document.createElement('div')
  success.classList.add('success')
  success.innerHTML = '<p>Ваше сообщение успешно отправлено!</p><br><p>Спасибо за оказанное доверие.</p><p>В ближайшее время наш менеджер свяжется с Вами.</p>'
  formElement.appendChild(success)
  let arrCheckValid = []
  const validationOptions = [{
      attribute: 'required',
      isValid: input => input.value.trim() != '',
      errorMessage: (input, label) => `Заполните поле`
    },
    {
      attribute: 'pattern',
      isValid: input => {
        const patternRegExp = new RegExp(input.pattern)
        return patternRegExp.test(input.value)
      },
      errorMessage: (input, label) => {
        let m = ''
        if (input.classList.contains('email')) {
          m = `Email не дописан`
        }
        if (input.type == 'tel') {
          m = `Номер не дописан`
        }
        return m
      }
    },
    {
      type: 'checkbox',
      isValid: input => input.checked,
      errorMessage: (input) => 'Подтвердите свое согласие'
    },
  ]

  let validateSingleFormGroup = formGroup => {
    const label = formGroup.querySelector('label')
    const input = formGroup.querySelector('input, textarea')
    const errorContainer = formGroup.querySelector('.error')
    const errorIcons = formGroup.querySelector('.form-group__error-icons')

    let formGroupError = false
    for (const option of validationOptions) {
      if (!input.disabled && input.hasAttribute(option.attribute) && !option.isValid(input)) {
        errorContainer.textContent = option.errorMessage(input, label)
        errorIcons.firstElementChild.classList.remove('active')
        errorIcons.lastElementChild.classList.add('active')
        input.classList.remove('ok')
        input.classList.add('failed')
        formGroupError = true
        arrCheckValid.push(false)
        console.log(input.value)
      }
      if (input.type == option.type && !option.isValid(input)) {
        errorContainer.textContent = option.errorMessage(input, label)
        formGroupError = true
        arrCheckValid.push(false)
      }
    }
    if (formGroupError === false) {
      errorContainer.textContent = ''
      if (errorIcons) {
        errorIcons.children[0].classList.add('active')
        errorIcons.children[1].classList.remove('active')
        input.classList.remove('failed')
        input.classList.add('ok')
      }
    }
  }

  formElement.setAttribute('novalidate', '')
  Array.from(formElement.querySelectorAll('.form-group input')).forEach(element => {
    if (element.type == 'checkbox') {
      element.parentElement.addEventListener('click', (e) => {
        validateSingleFormGroup(e.target.parentElement)
      })
    }
    if (element.type != 'checkbox') {
      element.addEventListener('input', (e) => {
        validateSingleFormGroup(e.target.parentElement)
      })
    }
    if (element.type != 'checkbox') {
      element.addEventListener('change', (e) => {
        validateSingleFormGroup(e.target.parentElement)
      })
    }
    if (element.type != 'checkbox') {
      element.addEventListener('focus', (e) => {
        validateSingleFormGroup(e.target.parentElement)
      })
    }
  })

  formElement.addEventListener('submit', (e) => {
    e.preventDefault()
    arrCheckValid = []
    validateFormGroups(formElement)
    let valid = arrCheckValid.filter(el => el == false)
    if (valid.length == 0) {

      const formData = new FormData(formElement)
      fetch('post.php', {
          method: 'post',
          body: formData
        })
        .then(function(response) {
          if (response.ok) {
            let overlay = document.createElement('div')
            overlay.classList.add('success-overlay')
            formElement.appendChild(overlay)
            success.classList.add('active')

            const inputs = formElement.querySelectorAll('.form-group input, textarea')
            inputs.forEach(input => {
              if (input.type != 'checkbox') {
                input.value = ''
                input.classList.remove('ok')
                let formGroup = input.closest('.form-group')
                if (formGroup) {
                  let icons = formGroup.querySelector('.form-group__error-icons')
                  if (icons) {
                    icons.children[0].classList.remove('active')
                  }
                }
              }
            })
            setTimeout(() => {
              success.classList.remove('active')
              overlay.remove()
            }, 5000)
          }
          return response.text()
        })
        .then(function(text) {
          console.log(text)
        })
        .catch(function(error) {
          console.error(error)
        })

      // formElement.submit()
    }
  })
  const validateFormGroups = formToValidate => {
    const formGroups = Array.from(formToValidate.querySelectorAll('.form-group'))
    formGroups.forEach(formGroup => {
      validateSingleFormGroup(formGroup)
    })
  }
}
