////////// INIT GSAP ////////////////////////////
import { gsap } from "gsap"
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Draggable } from 'gsap/Draggable'
import { InertiaPlugin } from './InertiaPlugin.min.js'
gsap.registerPlugin(ScrollTrigger, Draggable, InertiaPlugin)

////////// HORISONTAL CATALOGS ANIMATING ///////////////////
const catalog = document.querySelector('.catalog')
if (window.innerWidth > 768 && catalog) {
  const catalogs = gsap.utils.toArray('.catalog')
  const category_wraps = gsap.utils.toArray('.category-wrap')
  let gap, cur_win_width, win_width, container_width, catalogs_offset, header_bottom, nav_category, pin_offset
  let catalogs_items = category_wraps.map(el => { return el.querySelectorAll('.catalog__item') })

  initCatalog()

  gsap.set(catalogs, { paddingLeft: catalogs_offset })

  catalogs_items.forEach((element, index) => {
    let catalog_items_width = 0
    for (let item of element) {
      catalog_items_width += item.offsetWidth
    }
    let tl = gsap.timeline()
      .to(element, {
        x: -catalog_items_width + container_width - (element.length - 1) * gap,
        duration: 10,
        ease: 'linear',
      })
    let st = ScrollTrigger.create({
      trigger: catalogs[index],
      start: () => 'top ' + pin_offset,
      end: () => 'bottom ' + pin_offset,
      pin: true,
      scrub: 1,
      animation: tl,
    })
  })

  window.addEventListener('resize', () => {
    cur_win_width = window.innerWidth
    if (cur_win_width !== win_width) {
      // cur_win_width = win_width
      setTimeout(() => {
        location.reload()
      }, 500)
    }
  })

  ////// functions ///////////////////////
  function initCatalog() {
    header_bottom = document.querySelector('.header__bottom').offsetHeight
    nav_category = document.querySelector('.nav-category-wrap').offsetHeight
    pin_offset = header_bottom + nav_category + 10
    win_width = window.innerWidth
    container_width = document.querySelector('.container').offsetWidth
    catalogs_offset = (win_width - container_width) / 2
    getCatalogGap()
  }

  function getCatalogGap() {
    const cat = document.getElementById('catalog');
    const style = window.getComputedStyle(cat);
    gap = parseInt(style.columnGap);
  }
}

