////// ANIMATING CAR AND DOTS ////////////////////////////////////////////
let car = document.querySelector('.car')
if (car) {
  let tween, dot
  let html = document.querySelector('html')
  html.style.scrollBehavior = 'smooth'
  let scrollbarWidth = window.innerWidth - document.body.offsetWidth
  let car_img = car.querySelector('.car__img')
  let lights = car.querySelector('.car__lights')
  let car_overlay = document.querySelector('.car-overlay')
  let lists = car_overlay.querySelectorAll('.car-overlay__list-wrap')
  let car_dots = document.querySelectorAll('.car__dot')

  ScrollTrigger.create({
    id: "car",
    trigger: car,
    start: `top 50%`,
    end: `top 50%`,
    onEnter: onEnter,
  })

  function onEnter() {
    if (tween) return
    tween = gsap.timeline({})
      .to(lights, { opacity: 1, repeat: 3, yoyo: true, })
      .from('.car__dot', { autoAlpha: 0, scale: .5, ease: 'back.out(7)', stagger: .15 })
  }

  //---------- INTERACTIVE DOTS ----------------------------------//
  car_dots.forEach(dot => dot.addEventListener('click', onDot))
  car_overlay.addEventListener('click', onOverlay)

  function onDot(e) {
    let target = e.currentTarget
    car_img.scrollIntoView({
      block: 'center'
    })
    setTimeout((e) => {
      // html ------------------------------
      html.classList.add('fixed')
      html.style.paddingRight = scrollbarWidth + 'px'
      car_overlay.classList.add('active')
        // dot ------------------------------
      dot = document.createElement('div')
      dot.classList.add('car__dot', 'active')
      let rect = target.getBoundingClientRect();
      dot.style.top = rect.top + 'px'
      dot.style.left = rect.left + 'px'
      car_overlay.appendChild(dot)
      let corner = target.getAttribute('data-dot')
        // list ------------------------------
      lists.forEach(list => {
        if (list.classList.contains(corner)) {
          list.classList.add('active')
        }
      })
    }, 500)
  }

  function onOverlay() {
    html.classList.remove('fixed')
    car_overlay.classList.remove('active')
    dot.remove()
    html.style.paddingRight = 'unset'
    lists.forEach(list => {
      list.classList.remove('active')
    })
  }
}

