///////////// HOME PAGE SLIDER ///////////////////////////////
let slider_home = document.querySelector('.slider-home')
if (slider_home) {
  let track = slider_home.querySelector('.slider-home__slides')
  let slides = gsap.utils.toArray('.slider-home__slide')
  let slide_h = slides[0].offsetHeight

  let track_imgs = slider_home.querySelector('.slider-home__imgs')
  let imgs = Array.from(track_imgs.children)

  let pagination = slider_home.querySelector('.slider-home__pagination')
  let inners = gsap.utils.toArray('.slider-home__inner')

  let tl

  slides.forEach((slide, index) => {
    slide.setAttribute('data-index', index + 1)
    imgs[index].setAttribute('data-index', index + 1)
    let btn = document.createElement('button')
    btn.classList.add('slider-home__pagination-item')
    btn.setAttribute('data-index', index + 1)
    if (index === 0) {
      slide.classList.add('active')
      btn.classList.add('active')
    }
    pagination.append(btn)
  })

  imgs.reverse().forEach(img => { track_imgs.appendChild(img) })

  let pagination_items = gsap.utils.toArray(".slider-home__pagination-item")
  pagination_items.forEach((item) => item.addEventListener("click", onBtnClick))

  let delay = 4,
    duration = .75,
    ease = 'expo.inOut'

  tl = gsap.timeline({ paused: false })
    .to(track, { y: -slide_h, duration, delay, repeatDelay: delay, repeat: -1, ease, onRepeat: onRepeatSlides, onStart: onStartSlides })
    .to(imgs, { y: slide_h, duration, delay, repeatDelay: delay, repeat: -1, ease, onRepeat: onRepeatImgs }, 0)

  function onStartSlides() {
    slides.forEach((slide) => slide.classList.remove("active"))
    track.querySelector(`[data-index='2']`).classList.add("active")
    pagination_items.forEach((item) => item.classList.remove("active"))
    pagination.querySelector(`[data-index='2']`).classList.add("active")
  }

  function onRepeatSlides() {
    slides.forEach((slide) => slide.classList.remove("active"))
    track.appendChild(track.firstElementChild)
    track.children[1].classList.add("active")
    pagination_items.forEach((item) => item.classList.remove("active"))
    let index = parseInt(track.firstElementChild.getAttribute("data-index"))
    if (index === pagination_items.length) {
      pagination.querySelector(`[data-index='1']`).classList.add("active")
    } else {
      pagination.querySelector(`[data-index='${index + 1}']`).classList.add("active")
    }
  }

  function onRepeatImgs() {
    track_imgs.prepend(track_imgs.lastElementChild)
  }

  inners.forEach(inner => inner.addEventListener('mouseover', function(e) {
    let target = e.currentTarget.parentElement
    let style = window.getComputedStyle(target)
    let opacity = style.getPropertyValue("opacity")
    if (opacity > .9) tl.pause()
  }))
  inners.forEach(inner => inner.addEventListener('mouseleave', () => { tl.resume() }))

  function onBtnClick(e) {
    let target = e.currentTarget
    let index = target.getAttribute("data-index")
    let slide_t = track.querySelector(`[data-index='${index}']`).offsetTop
    let f = slide_t / slide_h;
    if (f === pagination_items.length) {
      f = 1
    } else if (f === 0) {
      f = pagination_items.length - 1
    } else { f-- }
    for (let i = 0; i < f; i++) {
      onRepeatSlides()
      onRepeatImgs()
    }
  }
}

///////////// SLIDER HISTORY //////////////////////////////////
let slider_history = document.querySelector('.slider-history')
if (slider_history) {
  let track = document.querySelector(".track");
  let slides = gsap.utils.toArray(".slide");
  let slide_w = slides[0].offsetWidth;
  let scrollbar = document.querySelector(".scrollbar");
  let scrollbar_w = scrollbar.offsetWidth;
  let thumb = scrollbar.firstElementChild
  let dates = document.querySelector('.dates')
  let dates_track = dates.firstElementChild
  let dates_track_h = dates_track.scrollHeight
  let dates_item_h = dates_track.children[0].offsetHeight
  let track_mis = (window.innerWidth - slide_w) / 2

  gsap.set(thumb, { skewX: '15deg' });
  gsap.set(track, { marginInlineStart: track_mis })

  Draggable.create(track, {
    type: "x",
    inertia: true,
    edgeResistance: 0.99,
    dragResistance: 0.25,
    bounds: {
      minX: 0,
      maxX: -track.scrollWidth
    },
    snap: { x: snapSlides() },
    onDrag() {
      gsap.set(thumb, { x: track_transform(this.x) });
      gsap.set(dates_track, { y: dates_transform_slider(this.x) });
    },
    onThrowUpdate() {
      gsap.set(thumb, { x: track_transform(this.x) });
      gsap.set(dates_track, { y: dates_transform_slider(this.x) });
    },
  });

  function snapSlides() {
    let arr = [];
    slides.forEach((slide, index) => {
      arr.push(index * -slides[0].offsetWidth);
    });
    return arr;
  }

  Draggable.create(thumb, {
    type: "x",
    inertia: true,
    bounds: {
      minX: 0,
      maxX: scrollbar_w
    },
    edgeResistance: 1,
    dragResistance: 0.25,
    snap: { x: snapScrollbar() },
    onDrag() {
      gsap.set(track, { x: thumb_transform(this.x) });
      gsap.set(dates_track, { y: dates_transform_scrollbar(this.x) });
    },
    onThrowUpdate() {
      gsap.set(track, { x: thumb_transform(this.x) });
      gsap.set(dates_track, { y: dates_transform_scrollbar(this.x) });
    }
  });

  function snapScrollbar() {
    let arr = [];
    slides.forEach((slide, index) => {
      arr.push((index * scrollbar_w) / (slides.length - 1));
    });
    return arr;
  }

  let thumb_transform = gsap.utils.mapRange(0, scrollbar_w, 0, -track.scrollWidth + slide_w);
  let track_transform = gsap.utils.mapRange(0, -track.scrollWidth + slide_w, 0, scrollbar_w);
  let dates_transform_slider = gsap.utils.mapRange(0, -track.scrollWidth + slide_w, 0, -dates_track_h + dates_item_h);
  let dates_transform_scrollbar = gsap.utils.mapRange(0, scrollbar_w, 0, -dates_track_h + dates_item_h);

  //====== CHANGING SLIDE ELEMENT PROPS ON DRAGGING =======//

  gsap.ticker.add(changeAllSlides);

  function changeAllSlides() {
    slides.forEach((slide) => {
      changeSlideParams(slide);
    });
  }

  function changeSlideParams(elem) {
    let content = elem.firstElementChild;
    let img = content.querySelector('.slider-history__img')
    let desc = content.querySelector('.slider-history__desc')
    let factor = 0;
    let el_h_w = elem.offsetWidth / 2;
    let slide_center = elem.getBoundingClientRect().left + el_h_w;
    let center = window.innerWidth / 2;

    if (slide_center < center) {
      factor = ((slide_center * 100) / center) * 0.01;
      factor = clamper(factor);
      factor = Math.pow(factor, 0.25);
      if (window.innerWidth > 992) {
        gsap.set(img, { scale: factor, });
        gsap.set(desc, { opacity: factor, scale: factor, });
      } else {
        gsap.set(img, { scale: 1, });
        gsap.set(desc, { opacity: 1, scale: 1, });
      }
    } else if (slide_center >= center) {
      factor = 1 - (((slide_center - center) * 100) / center) * 0.01;
      factor = clamper(factor);
      factor = Math.pow(factor, 0.25);
      if (window.innerWidth > 992) {
        gsap.set(img, { scale: factor, });
        gsap.set(desc, { opacity: factor, scale: factor, });
      } else {
        gsap.set(img, { scale: 1, });
        gsap.set(desc, { opacity: 1, scale: 1, });
      }
    }
  }
  let clamper = gsap.utils.clamp(0, 1);
}

///////////// ARTIKUL POTREB TOVARY DETAILS ///////////////////
