const tabs_acc = document.querySelector('[data-gar-tabs]')
if (tabs_acc) {
  const nav = tabs_acc.querySelector('.tabs-acc__tabs')
  const tabs = tabs_acc.querySelectorAll('.tabs-acc__tab')
  const contents = tabs_acc.querySelectorAll('.tabs-acc__content')
  let isFixed = false
  tabs.forEach((tab, index) => {
    if (index == 0) tab.classList.add('active')
    tab.setAttribute('data-index', index)
    tab.addEventListener('click', onTabClick)
  })
  contents.forEach((content, index) => {
    if (index != 0) content.classList.add('hidden')
    content.setAttribute('data-index', index)
  })

  function onTabClick(e) {
    tabs.forEach(tab => tab.classList.remove('active'))
    const index = e.currentTarget.getAttribute('data-index')
    e.currentTarget.classList.add('active')
    const btn = tabs_acc.querySelector(`.tabs-acc__btn[data-index="${index}"]`)
    contents.forEach(content => content.classList.add('hidden'))
    const content = tabs_acc.querySelector(`.tabs-acc__content[data-index="${index}"]`)
    content.classList.remove('hidden')
  }

  function onBtnClick(e) {
    const index = e.currentTarget.getAttribute('data-index')
    e.currentTarget.classList.add('active')
    tabs.forEach(tab => tab.classList.remove('active'))
    const tab = tabs_acc.querySelector(`.tabs-acc__tab[data-index="${index}"]`)
    tab.classList.add('active')
    contents.forEach(content => content.classList.add('hidden'))
    const content = tabs_acc.querySelector(`.tabs-acc__content[data-index="${index}"]`)
    content.classList.remove('hidden')
  }
  window.addEventListener('scroll', onScroll)

  function onScroll(e) {
    if (tabs_acc.getBoundingClientRect().top <= 40 && isFixed == false) {
      nav.classList.add('fixed')
      contents.forEach(content => content.classList.add('fixed'))
      isFixed = true
    } else if (tabs_acc.getBoundingClientRect().top > 40 && isFixed == true) {
      nav.classList.remove('fixed')
      contents.forEach(content => content.classList.remove('fixed'))
      isFixed = false
    }
  }
}

