////////////  F A Q  T A B S  ///////////////////////////////////////
const tabs_faq = document.querySelector('.tabs-faq')
if (tabs_faq) {
  const placeholder = tabs_faq.querySelector('.tabs-faq__placeholder')
  const cur = placeholder.querySelector('.cur')
  const tabs = tabs_faq.querySelectorAll('[role="tab"]')
  const tabList = tabs_faq.querySelector('[role="tablist"]')
  let isFixed = false
  let win_width = window.innerWidth

  if (window.innerWidth <= 900) {
    tabs[0].classList.add('hidden')
  }

  placeholder.addEventListener('click', () => {
    let tabList_top = tabList.getBoundingClientRect().top

    if (placeholder.classList.contains('fixed')) {
      const header_bottom_height = document.querySelector('.header__bottom-inner').offsetHeight
      window.scrollTo({
        top: window.scrollY + tabList_top - header_bottom_height - placeholder.offsetHeight,
        behavior: 'smooth'
      })
    }

    togglePlaceholder()

    if (tabs[6].classList.contains('hidden')) {
      tabList.style.borderBottom = '1px solid var(--clr-border)'
    } else {
      tabList.style.borderBottom = 'none'
    }

  })

  tabList.addEventListener('click', (e) => {
    let tabList_top = tabList.getBoundingClientRect().top
    tabs.forEach(tab => tab.classList.remove('hidden'))
    cur.innerText = e.target.innerText
    e.target.classList.add('hidden')

    if (placeholder.classList.contains('fixed')) {
      const header_bottom_height = document.querySelector('.header__bottom-inner').offsetHeight
      window.scrollTo({
        top: window.scrollY + tabList_top - header_bottom_height - placeholder.offsetHeight,
        behavior: 'smooth'
      })
    }

    togglePlaceholder()
  })

  function togglePlaceholder() {
    placeholder.classList.toggle('active')
    placeholder.nextElementSibling.classList.toggle('active')
  }

  window.addEventListener('resize', onResize)

  function onResize() {
    const win_cur_width = window.innerWidth
    if (win_cur_width > 900 && win_width < 900) {
      tabList.removeAttribute('style')
    }
    win_width = window.innerWidth
  }

  ///////// TABS WORK ////////////////////////////

  tabs.forEach((tab) => {
    tab.addEventListener("click", changeTabs)
  })
  let tabFocus = 0
  tabList.addEventListener("keydown", (e) => {
    // Move right
    if (e.key === "ArrowRight" || e.key === "ArrowLeft") {
      tabs[tabFocus].setAttribute("tabindex", -1);
      if (e.key === "ArrowRight") {
        tabFocus++;
        // If we're at the end, go to the start
        if (tabFocus >= tabs.length) {
          tabFocus = 0;
        }
        // Move left
      } else if (e.key === "ArrowLeft") {
        tabFocus--;
        // If we're at the start, move to the end
        if (tabFocus < 0) {
          tabFocus = tabs.length - 1;
        }
      }

      tabs[tabFocus].setAttribute("tabindex", 0);
      tabs[tabFocus].focus();
    }
  })

  function changeTabs(e) {
    const target = e.target
    const parent = target.parentNode
    const grandparent = parent.parentNode
    parent
      .querySelectorAll('[aria-selected="true"]')
      .forEach((t) => t.setAttribute("aria-selected", false))
    target.setAttribute("aria-selected", true)
    grandparent
      .querySelectorAll('[role="tabpanel"]')
      .forEach((p) => p.setAttribute("hidden", true))
    grandparent.parentNode
      .querySelector(`#${target.getAttribute("aria-controls")}`)
      .removeAttribute("hidden")
  }

  ///////////////////////////////////////////////////////////////////////////

  window.addEventListener('scroll', onScroll)

  function onScroll(e) {
    if (window.innerWidth <= 900) {
      if (tabs_faq.getBoundingClientRect().top <= 40 && isFixed == false) {
        placeholder.classList.add('fixed')

        tabList.style.marginTop = `${placeholder.offsetHeight}px`
        isFixed = true
      } else if (tabs_faq.getBoundingClientRect().top > 40 && isFixed == true) {
        placeholder.classList.remove('fixed')
        tabList.style.marginTop = `0px`
        isFixed = false
      }
    }
  }
}

