/////////// СТРАНИЦА СРАВНЕНИЯ ПРОДУКТОВ ///////////////////////
let sravn = document.querySelector('.art-potreb-sravn')
if (sravn) {
  //------ DRAGGABLE TABLE --------------------//
  let sravn = document.querySelector('.art-potreb-sravn')
  let sravn_w = sravn.scrollWidth
  let cont_w = document.querySelector('.container').offsetWidth
  let selects = document.querySelectorAll('.art-potreb-sravn__select')
  let curs = document.querySelectorAll('.art-potreb-sravn__cur')

  let mm = gsap.matchMedia()

  mm.add('(max-width: 768px)', () => {
    Draggable.create(sravn, {
      type: 'x',
      inertia: true,
      bounds: {
        minX: 0,
        maxX: -1 * (sravn_w - cont_w + 30),
      }
    })
  })

  //------ выпадающие меню --------------------//
  let cur_btns = Array.from(sravn.querySelectorAll('.art-potreb-sravn__cur'))
  cur_btns.forEach(btn => btn.addEventListener('click', onCur))

  function onCur(e) {
    let target = e.currentTarget
    if (target.classList.contains('active')) {
      target.classList.remove('active')
      target.nextElementSibling.classList.remove('active')
      sravn.style.paddingBottom = 0 + 'px'
      return
    }
    selects.forEach(select => select.classList.remove('active'))
    curs.forEach(cur => cur.classList.remove('active'))
    target.classList.toggle('active')
    target.nextElementSibling.classList.toggle('active')
    if (target.nextElementSibling.classList.contains('active')) {
      setTimeout(function() {
        sravn.style.paddingBottom = (target.nextElementSibling.offsetHeight - 60) + 'px'
      }, 300)
    } else {
      sravn.style.paddingBottom = 0 + 'px'
    }
  }

  let product_elems = gsap.utils.toArray('.art-potreb-sravn__prod')

  // заполняем таблицу сравнения
  product_elems.forEach((elem, i) => {
    createProductCard(products_obj[i].name, products_obj, product_elems[i])
  })

  // заполняем выпадающие списки с названиями продуктов
  let ul_items = gsap.utils.toArray('.art-potreb-sravn__select-inner')
  ul_items.forEach(el => {
    el.innerHTML = ''
    products_obj.forEach((prod, i) => {
      let li = document.createElement('li')
      li.classList.add('art-potreb-sravn__option')
      let div = document.createElement('div')
      div.classList.add('opt-img')
      let img = document.createElement('img')
      img.setAttribute('src', products_obj[i].img)
      let span = document.createElement('span')
      span.innerText = products_obj[i].name
      div.append(img)
      li.append(div, span)
      el.append(li)
    })
  })

  let li_btns = gsap.utils.toArray('.art-potreb-sravn__option')
  li_btns.forEach(btn => btn.addEventListener('click', (e) => {
    let name = e.currentTarget.querySelector('span').innerText
    let elem = e.currentTarget.closest('[data-index]')
    let index = elem.getAttribute('data-index')
    let tables = Array.from(product_elems[index - 1].querySelectorAll('.art-potreb-sravn__table'))
    tables.forEach(table => table.remove())
    createProductCard(name, products_obj, product_elems[index - 1])
    e.currentTarget.closest('.art-potreb-sravn__select').classList.toggle('active')
    e.currentTarget.parentElement.parentElement.previousElementSibling.classList.toggle('active')
    sravn.style.paddingBottom = 0 + 'px'
    doEqualRowsHeights()
  }))

  // функция создания карточки продукта для сравнения
  function createProductCard(name, product_obj, product_elem) {
    // найти нужный массив используя name
    let prod = product_obj.filter(el => el.name === name)

    // вставляем картинку в top блок
    let img = product_elem.querySelector('.art-potreb-sravn__img img')
    img.setAttribute('src', prod[0].img)

    // вставляем URL в ссылку-кнопку внизу карточки
    let a = product_elem.querySelector('.button')
    a.setAttribute('href', prod[0].link)

    // вставляем название продукта в top блок
    product_elem.querySelector('.art-potreb-sravn__cur .name').innerText = name

    // создаем таблицы с характеристиками продукта
    prod[0].tables.forEach((elems, ind) => {
      // создать div c классом art-potreb-sravn__table
      let table = document.createElement('div')
      table.classList.add('art-potreb-sravn__table')
      elems.forEach((el, i) => {
        if (i === 0) {
          // создать h4 c классом art-potreb-sravn__table-title
          let h4 = document.createElement('h4')
          h4.classList.add('art-potreb-sravn__table-title')
          h4.innerText = el
          table.append(h4)
        } else {
          // создать div c классом art-potreb-sravn__table-item
          let item = document.createElement('div')
          item.classList.add('art-potreb-sravn__table-item')
            // создать span
          let span1 = document.createElement('span')
          span1.innerText = el[0]
          item.append(span1)
            // создать span
          let span2 = document.createElement('span')
          span2.innerText = el[1]
          item.append(span2)
          table.append(item)
        }
        product_elem.querySelector('.button').before(table)
      })
    })
  }
  //---- функция выравнивания по высоте рядов в таблице -----
  function doEqualRowsHeights() {
    let items1 = document.querySelectorAll('.art-potreb-sravn__prod.one .art-potreb-sravn__table-item')
    let items2 = document.querySelectorAll('.art-potreb-sravn__prod.two .art-potreb-sravn__table-item')
    let items3 = document.querySelectorAll('.art-potreb-sravn__prod.three .art-potreb-sravn__table-item')
    let length = items1.length

    items1.forEach(item => item.removeAttribute('style'))
    items2.forEach(item => item.removeAttribute('style'))
    items3.forEach(item => item.removeAttribute('style'))

    for (let i = 0; i < length; i++){
      let arr = [items1[i], items2[i], items3[i]]

      let h = Math.max(arr[0].offsetHeight, arr[1].offsetHeight, arr[2].offsetHeight)
      arr.forEach(item => {
        item.style.height = `${h}px`
      })
    }
  }
  doEqualRowsHeights()
}

