////////// PROF FILTER ////////////////////////////////////
let filter = document.querySelector('[data-prof-filter]')
if (filter) {
  let filter_groups = filter.querySelectorAll('.filter__group')
  let select_wraps = filter.querySelectorAll('.filter__select-wrap')
  let filter_input_wraps = filter.querySelectorAll('.filter__input')
  let filter_inputs = filter.querySelectorAll('.filter__input input:first-child')
  let filter_arrows = filter.querySelectorAll('.filter__input svg')
  let filter_list_items = filter.querySelectorAll('.filter__list-item')
  let arr = []

  filter_groups.forEach((group, i) => group.setAttribute('data-index', i))

  fillArr()

  filter_input_wraps.forEach(el => el.addEventListener('click', onFilterInput))
  filter_list_items.forEach(el => el.addEventListener('click', onFilterListItem))

  function onFilterInput(e) {
    desactivate()
    let bool
    if (e.currentTarget.closest('.filter__group').getAttribute('data-index') != 0) {
      let prev = e.currentTarget.closest('.filter__group').previousElementSibling
      bool = prev.querySelector('.filter__input input').value != ''
    }

    if (e.currentTarget.closest('.filter__group').getAttribute('data-index') == 0 || bool) {
      openMenu(e)
    } else {
      let ind = parseInt(e.currentTarget.closest('.filter__group').getAttribute('data-index'))
      if (arr[ind - 1] === '') return
        // else openFirstMenu(e)
    }
  }

  function onFilterListItem(e) {
    desactivate()
    let target = e.currentTarget
    let name = target.innerText
    target.closest('.filter__group').querySelector('[type="text"]').value = name
    let num = target.closest('.filter__group').getAttribute('data-index')
    emptyInputValues(num)
    fillArr()
    if (target.closest('.filter__group').getAttribute('data-index') < arr.length - 1) {
      openNextMenu(e)
    }
  }

  function desactivate() {
    filter_groups.forEach(el => el.classList.remove('active'))
    select_wraps.forEach(el => el.classList.remove('active'))
    filter_inputs.forEach(el => el.classList.remove('active'))
    filter_arrows.forEach(el => el.classList.remove('active'))
  }

  function openMenu(e) {
    let target = e.currentTarget
    target.closest('.filter__group').classList.add('active')
    target.nextElementSibling.classList.add('active')
    target.querySelector('input').classList.add('active')
    target.querySelector('svg').classList.add('active')
  }

  function openNextMenu(e) {
    let target = e.currentTarget
    let next = target.closest('.filter__group').nextElementSibling
    next.classList.add('active')
    let input = next.querySelector('.filter__input')
    input.nextElementSibling.classList.add('active')
    input.querySelector('input').classList.add('active')
    input.querySelector('svg').classList.add('active')
  }

  function fillArr() {
    arr = []
    filter_inputs.forEach(input => arr.push(input.value))
  }

  function emptyInputValues(num) {
    filter_inputs.forEach((input, i) => {
      if (i > num) input.value = ''
    })
  }

  filter_arrows.forEach(arrow => arrow.addEventListener('click', onArrow))

  function onArrow(e) {
    let target = e.currentTarget
    if (!target.classList.contains('active')) return
    e.stopPropagation()
    target.classList.remove('active')
    target.parentElement.nextElementSibling.classList.remove('active')
    target.parentElement.parentElement.classList.remove('active')
    target.previousElementSibling.classList.remove('active')
  }

  // закрываем все открытые меню инпутов фильтра, если кликнули вне фильтра
  document.querySelector('.main').addEventListener('click', resetFilter)

  function resetFilter(e) {
    if (e.target.closest('.filter__group')) return
    filter_arrows.forEach(arrow => {
      arrow.classList.remove('active')
      arrow.parentElement.nextElementSibling.classList.remove('active')
      arrow.parentElement.parentElement.classList.remove('active')
      arrow.previousElementSibling.classList.remove('active')
    })
  }
}

